import React from 'react';

const BannerPromocional = () => {
  return (
    <section style={styles.banner}>
      <div style={styles.textContainer}>
        <h2 style={styles.mainText}>¡EL CHEL MÁS CERCA DE TI!</h2>
        <p style={styles.subText}>Escríbenos y haz tu pedido para llevar o a domicilio</p>
        <a
          href="https://wa.me/7771861658"
          target="_blank"
          rel="noopener noreferrer"
          style={styles.callToAction}
        >
          <img
            src={`${process.env.PUBLIC_URL}/img/Whatsapp.jpg`}
            alt="WhatsApp"
            style={styles.icon}
          />
          LLÁMANOS YA
        </a>
      </div>
      <div style={styles.imageContainer}>
        <img
          src={`${process.env.PUBLIC_URL}/img/promo.png`}
          alt="Promoción"
          style={styles.image}
        />
      </div>
    </section>
  );
};

const styles = {
  banner: {
    display: 'flex',
    flexDirection: 'row', // Alineación horizontal en pantallas grandes
    flexWrap: 'wrap', // Ajusta los elementos automáticamente
    justifyContent: 'center',
    alignItems: 'center',
    padding: '3% 5%',
    background: 'linear-gradient(to bottom, #ff9800, #ff6f00)',
    boxShadow: '0px 2px 5px rgba(0, 0, 0, 0.2)',
    fontFamily: '"Arial", sans-serif',
    borderRadius: '10px',
    margin: '0 auto',
    overflowX: 'hidden', // Evita desbordamiento horizontal
  },
  textContainer: {
    flex: '1 1 100%', // Ocupa el ancho completo en móviles
    textAlign: 'center',
    color: '#fff',
    padding: '1rem',
  },
  mainText: {
    fontSize: 'clamp(1.8rem, 4vw, 2.5rem)',
    fontWeight: 'bold',
    marginBottom: '0.5rem',
    textShadow: '1px 1px 3px rgba(0, 0, 0, 0.3)',
  },
  subText: {
    fontSize: 'clamp(1rem, 2.5vw, 1.3rem)',
    marginBottom: '1rem',
  },
  callToAction: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    gap: '0.5rem',
    backgroundColor: '#25d366',
    color: '#fff',
    textDecoration: 'none',
    padding: '0.5rem 1rem',
    borderRadius: '5px',
    fontWeight: 'bold',
    fontSize: 'clamp(0.8rem, 2vw, 1rem)',
    boxShadow: '0 2px 4px rgba(0,0,0,0.2)',
    width: 'fit-content',
    margin: '0 auto',
  },
  icon: {
    width: '1.2rem',
    height: '1.2rem',
  },
  imageContainer: {
    flex: '1 1 100%', // Imagen toma el ancho completo en móviles
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    padding: '1rem',
  },
  image: {
    width: '100%',
    maxWidth: '400px',
    height: 'auto',
    objectFit: 'cover',
    borderRadius: '10px',
  },
};

export default BannerPromocional;
